import { Settings } from "../config/commonconfig";
import * as microsoftTeams from "@microsoft/teams-js";
import * as  queryString from 'query-string';

export class SSOSearvice {
    private applicationConfig: any;
    constructor() {
        this.applicationConfig = {
            clientID: "e0f60ff0-327b-43e2-9055-54782bbed141",
            teamsAPI: ["User.Read"],
            campusApI: ["https://campuswebapi.azurewebsites.net/user_impersonation"]
        }
    }

    // In-line code
    public getFinalToken() {
        this.getAccessTokenClient().then((clientSideToken) => {
            return this.getAccessTokenServer(clientSideToken);
        }).then((serverSideToken) => {
            console.log("from server : ", serverSideToken);
            return serverSideToken;
        }).catch((error) => {
            if (error === "invalid_grant") {
                this.requestConsent().then((result: any) => {
                    // Consent succeeded - use the token we got back
                    let accessToken = JSON.parse(result).accessToken;
                    console.log("from concent : ", accessToken);
                    return accessToken;
                })
                    .catch((error) => {
                        return ""
                    });
            }
            else {
                // Something else went wrong
                return ""
            }
        });
    }



    // 1. Get auth token
    // Ask Teams to get us a token from AAD

    public getAccessTokenClient(): any {
        return new Promise((resolve, reject) => {
            microsoftTeams.authentication.getAuthToken({
                successCallback: (result) => {
                    resolve(result);
                },
                failureCallback: function (error) {
                    reject("Error getting token: " + error);
                }
            });

        });

    }

    // 2. Exchange that token for a token with the required permissions
    //    using the web service (see /auth/token handler in app.js)
    public getAccessTokenServer(clientSideToken) {
        return new Promise((resolve, reject) => {
            microsoftTeams.getContext((context) => {
                this.getToken(context.tid, clientSideToken).then((response) => {
                    if (response.ok) {
                        return response.json();
                    } else {
                        reject(response);
                    }
                })
                    .then((responseJson) => {
                        if (responseJson.error) {
                            reject(responseJson.error);
                        } else {
                            const serverSideToken = responseJson;
                            resolve(serverSideToken);
                        }
                    });
            });
        });
    }

    // Show the consent pop-up
    public requestConsent() {
        return new Promise((resolve, reject) => {
            microsoftTeams.authentication.authenticate({
                url: window.location.origin + "/SSO/auth-start",
                width: 600,
                height: 535,
                successCallback: (result) => {
                    if (result) {
                        let data = localStorage.getItem(result);
                        localStorage.removeItem(result);
                        resolve(data);
                    }
                },
                failureCallback: (reason) => {
                    reject(JSON.stringify(reason));
                }
            });
        });
    }


    public getToken(tid?: string, token?: string): Promise<any> {
        var scopes = ["https://graph.microsoft.com/User.Read"];

        return new Promise((resolve, reject) => {
            const url = "https://login.microsoftonline.com/" + tid + "/oauth2/v2.0/token";
            const params = {
                client_id: Settings.AppSettings.appId,
                client_secret: Settings.AppSettings.appSecret,
                grant_type: "urn:ietf:params:oauth:grant-type:jwt-bearer",
                assertion: token,
                requested_token_use: "on_behalf_of",
                scope: scopes.join(" ")
            };

            fetch(url, {
                method: "POST",
                body: queryString.stringify(params),
                headers: {
                    Accept: "application/json",
                    "Content-Type": "application/x-www-form-urlencoded"
                }
            }).then(result => {
                if (result.status !== 200) {
                    result.json().then(json => {
                        // TODO: Check explicitly for invalid_grant or interaction_required
                        reject({ "error": json.error });
                    });
                } else {
                    result.json().then(json => {
                        resolve(json.access_token);
                    });
                }
            });
        });
    }
}
